import { useEffect, useState } from 'react'
import { Table, Radio, MessagePlugin } from "tdesign-react";
import { apiTraceServer } from "../../api/api"
import  NoData  from '../../components/NoData'
import moment from 'moment'

function Server (props: any) {
  const [page, setPage] = useState(1)
  const [pageSize, setPageSize] = useState(10)
  const [data, setData] = useState([])
  const [total, setTotal] = useState(30)
  const [provider, setProvider] = useState("BAIDU");
  useEffect(() => {
    getData()
  }, [])
  
  function getData(item?:any){
    apiTraceServer({page, pageSize, provider: item?.provider ?? provider }).then((res: any) => {
      const { data } = res
      if (res.code === 0) {
        setData(data.items)
        setTotal(data.total)
      } else {
        MessagePlugin.error(res.msg ?? "请求出错！");
      }
    })
  }
 // 更改单选类型 - 不同服务商切换
 const changeHandle = (val: any) => {
  setProvider(val);
  getData({provider:val});
};
  return (
    <div className='container' key="server">
      <div className="searchCont">
        <div style={{'height':'50px'}}></div>
        <div className="tdesign-demo-block-row" >
          <Radio.Group value={provider} onChange={changeHandle}>
            <Radio value="BAIDU">百度地图</Radio>
            <Radio value="AMAP">高德地图</Radio>
            {/* <Radio value="QQ">腾讯地图</Radio> */}
          </Radio.Group>
        </div>
      </div>
      {data && data.length > 0? (<Table 
          data={data} 
          columns={[
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'id',
              title: '服务ID',
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'name',
              title: '服务名称',
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'created',
              cell({row}){
                return row.created ? moment(row.created).format("YYYY-MM-DD HH:mm:ss"): '--'
              },
              title: '创建时间',
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'updated',
              cell({row}){
                return row.updated ? moment(row.updated).format("YYYY-MM-DD HH:mm:ss") : '--'
              },
              title: '更新时间',
            },
            {
              colKey: "op",
              width: 120,
              title: "操作",
              fixed: "right",
              cell(record: any) {
                return (
                  <>
                    <a
                      className="link"
                      onClick={() => {
                        props.history.push({
                          pathname: `/server/${record.row.id}`,
                          state: record.row,
                        });

                      }}
                    >
                      查看终端
                    </a>
                  </>
                );
              },
            },
          ]} 
          rowKey="index" 
          verticalAlign="top"
          bordered
          hover
          pagination={{
            defaultCurrent: 2,
            defaultPageSize: 10,
            total,
            showJumper: true,
            onChange(pageInfo) {
              setPageSize(pageInfo.pageSize)
              setPage(pageInfo.current)
              getData()
            },
          }}
        ></Table>) : (<NoData />)}
    </div>
  )
}

export default Server;